<template>
	<div class="clearfix">
		<div class="companyMess">
			<h2 class="enterpriseName">{{companyMess.enterpriseName}}</h2>
			<ul class="companyMessList clearfix">
				<li><span class="titleName"></span><span class="titleValue">{{companyMess.person}}</span></li>
				<li><span class="titleName"></span><span class="titleValue">{{companyMess.companySize}}</span></li>
				<li><span class="titleName"></span><span class="titleValue">{{companyMess.industry}}</span></li>
				<li><span class="titleName"></span><span class="titleValue">{{companyMess.enterPark}}</span></li>
				<li><span class="titleName"></span><span class="titleValue">{{companyMess.employeesNumber}}</span></li>
				<li><span class="titleName"></span><span class="titleValue">{{companyMess.enterTime}}</span></li>
			</ul>
			<div class="switchEnterprise"><i></i></div>	
		</div>
		<div class="echartContainer clearfix">
			<div class="echartDetial myChart1">
				<h3><span></span></h3>
				<div class="chartBox">
					<radarCircle :echartsData="echartsData.radarData" @handleCharts="handleCharts"></radarCircle>
				</div>
			</div>
			<div class="echartDetial myChart2">
				<h3><span></span></h3>
				<div class="chartBox">
					<ul class="enterpriseList">
						<li class="clearfix" v-for="(item,index) in enterpriseLists"  :key="index">
							<i class="fl"></i>
							<span>{{item}}</span>
							<router-link class="enterpriseJumps fr" to="/build">
								
							</router-link>
						</li>
					</ul>
					<div class="pageBox">
						<elPageation
						:pageSize="pageSize"
						:layout="layout"
						:total="total"
						:currentPage="page"
						:pageCount="pageCount"
						@pageChange="pageChange"></elPageation>
					</div>
				</div>
			</div>
			<div class="echartDetial myChart3">
				<h3><span></span></h3>
				<div class="chartBox">
					<twoBar :echartsData="echartsData.twoBarData" @handleCharts="handleCharts"></twoBar>
				</div>
			</div>
			<div class="echartDetial myChart4">
				<h3><span></span></h3>
				<div class="chartBox clearfix">
					<div class="chart fl" v-for="(item,index) in echartsData.items" :key="index">
						<perPieUp :echartsData="{item:echartsData.items[index]}" @handleCharts="handleCharts"></perPieUp>
					</div>
				</div>
			</div>
			<div class="echartDetial myChart5">
				<h3><span></span></h3>
				<div class="chartBox clearfix">
					<div class="chart fl"  v-for="(item,index) in echartsData.items2" :key="index">
						<perPieUp :echartsData="{item:echartsData.items2[index]}" @handleCharts="handleCharts"></perPieUp>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import radarCircle from './components/radarCircle'
import twoBar from './components/twoBar'
import perPieUp from './components/perPie'
import elPageation from 'components/pagination/pagination'
export default {
	name: 'occupationRatioRAnalysis',
	data(){
		return {
			//
			pageSize: 5,
			total:50,
			pageCount: 10,
			layout: "total, prev,pager,next,jumper",
			page: 1,
			echartsData:{
				//
				items:[
					{
						titleName:'',
						changeColor:['#3a7fdd','#315fa3'],
						proportionType:'',
						possession:[35,65],
						proportion:'65%'
					},
					{
						titleName:'',
						changeColor:['#43a9ff','#2294f5'],
						proportionType:'',
						possession:[35,65],
						proportion:'65%'
					}
				],
				//
				items2:[
					{
						smallPic:true,
						titleName:'',
						changeColor:['#3a7fdd','#315fa3'],
						proportionType:'',
						possession:[35,65],
						proportion:'65%'
					},
					{
						smallPic:true,
						titleName:'',
						changeColor:['#43a9ff','#2294f5'],
						proportionType:'',
						possession:[56,44],
						proportion:'44%'
					},
					{
						smallPic:true,
						titleName:'',
						changeColor:['#40c2ff','#00baff'],
						proportionType:'',
						possession:[60,40],
						proportion:'40%'
					},
					{
						smallPic:true,
						titleName:'',
						changeColor:['#70de2d','#40c62a'],
						proportionType:'',
						possession:[65,35],
						proportion:'35%'
					}
				],
				//
				radarData:{
					legendData:['',''],
					indicatorData: [
						{ name: ''},
						{ name: ''},
						{ name: ''},
						{ name: ''},
						{ name: ''},
						{ name: ''}
					],
					seriesData:{
						enterpriseValue:[25000, 15000, 5000,15000,25000,20000],
						averageValue:[20000, 10000,3000,10000,20000,25000]
					}
				},
				//
				twoBarData:{
					xAxisData:['2013','2014','2015','2016','2017'],
					legendData:['',''],
					seriesData:[
						[2900, 4500, 2500, 3300, 2500],
						[2500, 3600, 2800, 2200, 3900]
					]
				}
			},
			//
			companyMess:{
				enterpriseName:'',
				person:'',
				companySize:'1000',
				industry:'',
				enterPark:'029-88899666',
				employeesNumber:'590',
				enterTime:'2013'
			},
			//
			enterpriseLists:[
				'',
				'',
				'',
				'',
				''
			]
		}
	},
	methods:{
	  //
      pageChange(val) {
        this.page = val;
        console.log(` ${val} ${this.pageSize}`)
      },
      handleCharts(data){
        console.log(data)
      }
    },
    components:{
      radarCircle,
	  twoBar,
	  perPieUp,
	  elPageation
    }
}
</script>

<style scoped>
	/**/
	.companyMess h2{
		color: #474747;
		font-weight: normal;
		height: 30px;
		margin: 5px 0;
	}
	.companyMess{
		position: relative;
	}
	.switchEnterprise{
		cursor: pointer;
		position: absolute;
		right: 3px;
		line-height: 34px;
		bottom: 0;
		color: #015293;
	}
	.switchEnterprise i{
		line-height: 28px;
		padding-left: 30px;
		height: 28px;
		display: inline-block;
		background: url(../../assets/img/switchIcon.png) no-repeat center left;
	}
	.companyMessList{
		width: 94%;
	}
	.companyMessList li{
		float: left;
		width: 33.3%;
	}
	.companyMessList li span{
		line-height: 34px;
		font-size: 14px;
	}
	.titleName{
		display: inline-block;
		width: 90px;
		color: #999;
	}
	.titleValue{
		color: #4c4c4c;
		font-weight: bold;
	}
	/*  */
	.echartContainer{
		margin-top: 10px;
		background-color: #f8f8f8;
		border: 1px solid #dedede;
		padding:0 14px 20px;
	}
	.echartDetial{
		float: left;
	}
	.chartBox{
		border: 1px solid #ccc;
		background-color: #fff;
	}
	.echartDetial h3{
		height: 56px;
		padding-left: 15px;
		font-size: 16px;
		font-weight: normal;
		line-height: 56px;
		position: relative;
	}
	.echartDetial h3 span{
		position: absolute;
		display: inline-block;
		top: 20px;
		left: 0;
		width: 5px;
		height: 16px;
		background-color: #015293;
	}
	.myChart1{
		width: 842px;
		margin-right: 14px;
	}
	.myChart2{
		width: 314px;
	}
	.enterpriseList{
		padding: 0 15px;
		border-bottom: 1px solid #e2e2e2;
	}
	.enterpriseList li{
		margin: 26px 0;
	}
	.enterpriseList span{
		line-height: 28px;
		font-size: 14px;
		color: #474747;
	}
	.enterpriseList .enterpriseJumps{
		line-height: 28px;
		color: #015293;
		font-size: 14px;
	}
	.enterpriseList .enterpriseJumps:hover{
		opacity: .7;
	}
	.enterpriseList i{
		line-height: 28px;
		height: 28px;
		width: 28px;
		display: inline-block;
		background: url(../../assets/img/positionIcon.png);
	}
	.myChart1 .chartBox,.myChart2 .chartBox{
		height: 365px;
	}
	.myChart3,.myChart4,.myChart5{
		width: 100%;
	}
	.myChart3 .chartBox{
		height: 445px;
	}
	.myChart4 .chartBox,.myChart5 .chartBox{
		height: 334px;
	}
	.myChart4 .chart{
		width: 50%;
		height: 334px;
	}
	.myChart5 .chart{
		width: 25%;
		height: 334px;
	}
	.pagination-wrap{
		margin: 15px 0px;
	}
</style>


